import React, {Component} from 'react';
import {Card,CardImg} from 'reactstrap';

class LandingPage extends Component{
    render(){
        return(
            <div className="row">
                <div className="col-lg-6 col-sm-12">
                    <Card>
                        <CardImg src = 'assets/images/landing1.png'/>
                    </Card>
                </div>
                <div className="col-lg-6 col-sm-12">
                <Card>
                        <CardImg src = 'assets/images/landing2.png'/>
                    </Card>
                </div>
                <div className="col-lg-6 col-lg-push-6 col-sm-12"> <Card>
                    <CardImg src = 'assets/images/landing3.png'/>
                </Card></div>
                <div className="col-lg-6 col-lg-pull-6 col-sm-12"> <Card>
                    <CardImg src = 'assets/images/landing1.png'/>
                </Card></div>
            </div>
        )
    }
}


export default LandingPage;