import React, { useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Modal,ModalBody,Form,Input,ModalFooter,
  Button,Label, FormGroup
} from 'reactstrap';
import {FcGoogle} from 'react-icons/fc';

import {AiFillFacebook,AiFillGithub} from 'react-icons/ai';


const Header = (props) => {

    const [isOpen, setIsOpen] = useState(false);
    const [isModalOpen, setModalOpen] = useState(false);
    const toggle = () => setIsOpen(!isOpen);
    const toggleModal = () => setModalOpen(!isModalOpen);
    return (
        <div>
        <Navbar   fixed="top" expand="md" light   >
            <NavbarBrand href="/"><img style = {{height:'40px'}}src = '/assets/images/spark_logo.png'/></NavbarBrand>
            <NavbarToggler onClick={toggle} />
            <Collapse isOpen={isOpen} navbar>
            <Nav className="ml-auto" navbar dark>
                <NavItem>
                <NavLink href="https://www.github.com/jironemo" target = '_blank'>GitHub</NavLink>
                </NavItem>
                <NavItem >
                <NavLink style={{color: 'blue'}} href="/">Create Account</NavLink>
                </NavItem>
            </Nav>
            <Button color = 'primary' onClick = {toggleModal}>Login</Button>
            <Modal centered = {true} isOpen={isModalOpen} toggle={toggleModal}>
                   <ModalBody>
                        <div className = 'container'>
                        <Form>
                                <FormGroup>
                                    <Label htmlFor = 'username'>Username</Label>
                                    <Input type = 'text' placeholder = 'username' name = 'username'id = 'username'/>
                                </FormGroup>
                           
                                <FormGroup>
                                    <Label htmlFor = 'password'>Password</Label>
                                    <Input type = 'password' placeholder = 'password' name='password'id = 'password'/>
                                </FormGroup>
                                <FormGroup check>
                                        <Label className = 'float-left' check>
                                          <Input type="checkbox" check />{' '}
                                          Remember me.
                                        </Label>

                                        <Button color = 'link' className = 'float-right'> Forgot Password?</Button>
                                      </FormGroup>
                                <Button style = {{backgroundColor: '#0178bd'}} type = 'submit' className = 'col-12'>Login</Button>
                            </Form>
                        </div>
                    </ModalBody>
                    <ModalFooter className = 'justify-content-center'>
                        Login with:
                        <a primary target = '_blank' href = "https://www.facebook.com"><AiFillFacebook style = {{fontSize:'30px'}}/></a>
                        <a primary target = '_blank' href = "https://www.google.com"><FcGoogle size = {25}/></a>
                        <a primary target = '_blank' href = "https://www.google.com"><AiFillGithub style = {{fontSize:'30px' ,color:'black'}}/></a>
                        
                    </ModalFooter>
                  </Modal>
            </Collapse>
            
        </Navbar>
        </div>
    );

}

export default Header;